*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
.servicedisplay{
    display: flex;
    flex-direction: row;
    margin-bottom: 9%;
    
    
}
.servicepart{
    border: 2px solid goldenrod;border-radius: 12px;
    background-color: cadetblue;
    width: 250px;
    height: 580px;
margin-left: 1%;
cursor: pointer;

}
.servicepart:hover{
    box-shadow: rgba(0, 0,0, 0.74) 0px 4px 8px;
    border: 2px solid peru;
    border-radius: 12px;
    transform: scale(0.9);
}
section{margin-left: 1%;}
.thead{
    font-family: 'Times New Roman', Times, serif;
    font-weight: 900;
    font-size: 32px;
    font-style: oblique;
    text-transform:capitalize;
    text-decoration: underline;
    color: darkred;
    text-transform: capitalize;
    margin-top: 2%;
    margin-bottom: 4%;
}
.serviceImage{ width: 150px;
    height: 160px;
    margin-left: 4%;
    border-radius: 12px;}
.servisepara{ font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-weight:500;
font-size: 23px;
margin-left: 4%;
margin-right: 4%;
margin-top: 4%;
margin-bottom: 5%;
letter-spacing: 2px;
color: aliceblue;}