*{margin:0 ;
padding: 0;
}
.container{
    display: flex;
    flex-direction:row;
    
}.left{
    margin-top:20px;
    margin-left: 2%;
    padding:20px 30px;
    letter-spacing: 2px;
}
.showground{margin-top:2%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;}
.carshow{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    border:none;
    margin-top: 3%;
    padding: 0%;
    margin-left: 1%;
    position: relative;
}.carshow:hover{
    box-shadow: rgba(0, 0,0, 0.74) 0px 4px 8px;
    border: 2px solid peru;
    border-radius: 12px;

}
.image{
    width: 200px;
    height: 200px;justify-content: center;
    align-items: center;
    padding: 0%;
    margin: 0;
}
.headtitle{
    margin-bottom: 3%;
    margin-top: 5%;
   font-family: 'Courier New', Courier, monospace;
   font-weight:500 ;
   font-size: 23px;
   text-transform:uppercase ;
   color:saddlebrown;
}.price{
    margin-left: 4%;
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 18px;
    font-weight: 700;
}
.purchase-btn{background-color: rgb(56, 95, 225);
    padding: 10px;
    color: white;
    border: none;
    border-radius: 14%;
    margin-top: 5%;
    margin-left: 8%;
    cursor:pointer;}
    .categories{position: sticky;
    top: 20%;
left: 9%;}
    .disk{
        text-decoration: none;
        list-style: none;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-weight: 800;
        font-style: italic;
        padding: 14px 52px;
        color: darkcyan;
        cursor: pointer;
        margin-bottom: 15%;
        box-shadow: rgba(0, 0,0, 0.74) 0px 4px 8px;

    }
    .disk:hover{
        transform:scale(1.2);
    }