*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}
.carbeatingpage{
    display: flex;
    flex-direction: column;
    margin-top: 5%;
}

.carhead{
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 34px;
    font-weight: 900;
    color: brown;
    text-transform:uppercase;
    text-decoration: underline;
    margin-bottom: 2%;

    
}
.carpara{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 2px;
    color: grey;
    text-align: center;
    margin-bottom: 2%;
    
}
.formdata{
    display: flex;
    flex-direction: column;
    margin-left: 15%;
    margin-right: 12%;
    margin-top: 3%;
    margin-bottom: 3%;
}
.personalinfo{
    display: flex;
    flex-direction: row;
    margin-top: 2%;
}
.formhandle{background-color: cadetblue;   
     display: flex;
    flex-direction: column;
    
height: 90%;}
label{
    color: blue;
    touch-action: cross-slide-y;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: larger;
    font-weight: 800;
    margin-left: 2%;
    margin-bottom: 2%;
}
input{
    margin-left: 3%;
    width:24% ;
    margin-bottom: 2%;
}
.ms{
    margin-left:14%;
    width:44% ;
    height: 7%;
}
#message{
    width:70% ;
    margin-left: 9%;
}
.checkbox{
    color:blue ;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: larger;
    font-weight: 500;
    
}
.btnApplication{
    text-align: center;

}
.mbtn{
    background-color: chocolate;
    color: white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 16px;
    font-weight: 100;
    margin-left: 2%;
    padding: 10px 32px;
    border: 2px solid chocolate;
    border-radius: 12px;
    cursor: pointer;
}
.mbtn:hover{
    background-color: powderblue;
    color: grey;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 16px;
    font-weight: 100;
    margin-left: 2%;
    border: 2px solid powderblue;
    border-radius:12px;
    padding: 10px 32px;
    cursor: pointer;
}