*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.navbarhead{
    position: relative;
}
.navbar{position:fixed;
    top: 0%;
    display: flex;
    flex-direction: row;
    background-color: darkblue;
    height: 8vh;
   width: 100%;
    z-index: 1;
}
.merge{
    margin-left: 12%;
    margin-top: 3vh;
}
.navhead{
    margin-left: 3%;
    margin-top: 2vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    font-size: 24px;
    font-weight: 800;
    color: white;
    text-decoration: wavy;
}
.navlink{text-decoration: none;
    list-style: none;
    padding: 3px 23px;
    border-radius: 3%;
    font-weight: 800;
    font-size: 17px;
   color:red;
}
.navlink:hover{
    
    border-radius: 19%;
    font-weight: 800;
    font-size: 17px;
   color:white;
   border-radius:4% ;
   background-color: brown;
   cursor: pointer;
}.search{
    margin-top: 2vh;
    margin-left: 1%;
    display: flex;
    flex-direction: row;
}
.searchnav{
    border: 3px solid rgb(56, 95, 225);
    padding: 10px;
    width: 200px;
    outline: none;

}
.navbtn{
    background-color: rgb(56, 95, 225);
    padding: 10px;
    height: 33px;
    color: white;
    border: none;
    cursor: pointer;
}
